.banner {
    margin-top: 70px;
    background-size: cover;
    height: 100vh;
    color: white;

}

.content {
    padding-top: 140px;
    height: 190;
    padding-left: 15px;
}

.title {
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}

.button {
    color: white;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 5px;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(51, 51, 51, 0.5);
    cursor: pointer;
    margin-right: 1rem;
}

.button:hover {
    color: black;
    background-color: #e6e6e6;
}

.description {
    width: 45rem;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 1rem;
    height: 80px;
    max-width: 400px;
}

@media screen and (max-width: 599px) {
    .description {
        max-height: 100px !important;
        max-width: 281px !important;
        overflow: scroll !important;

    }

    .banner {
        height: 50vh;
    }

    .description::-webkit-scrollbar {
        display: none;
    }

    .description {
        -ms-overflow-style: none;
        scrollbar-width: none;
    }

}


.bg-gradient {
    background: linear-gradient(45deg, black, transparent) !important;
    height: 100vh;
}

